.About {
    width: 100vw;
    margin-top: 110px;

    @media(min-width: 1024px) {
        width: 800px;
        margin-left: auto;
        margin-right: auto;
    }

    img {
        object-fit: contain;
        // height: 100vh;
        width: 100vw;

        @media(min-width: 1024px) {
            width: 800px;
        }
    }

    &__txt {
        padding: 15px;

        @media(min-width: 1024px) {
            padding: 45px 0;
        }
    }
}