.Gallery {
    &__mainPhoto {
        // background-image: url('../img/lake.jpg');
        width: 100vw;
        height: 100vh;
        // background-size: cover;
        // background-attachment: fixed;
        object-fit: cover;
    }

    &__photos {
        transform: scale(1);

        @media(min-width: 1024px) {
            // max-width: 1200px;
            // margin-left: auto;
            // margin-right: auto;

            background-color: white;
        }
    }

    &__photosFlexContainer {
        transform: scale(1);

        @media(min-width: 1024px) {
            max-width: 1200px;
            margin-left: auto;
            margin-right: auto;
            display: flex;
            flex-wrap: wrap;

            // background-color: rgb(70, 42, 42);
        }
    }

    &__photo {
        transform: scale(1);

        @media(min-width: 1024px) {
            overflow: hidden;
            flex-basis: 50%;
            max-width: 1200px;
            margin-left: auto;
            margin-right: auto;
        }

        img {
            transition: .3s;

            &:hover {
                transform: scale(1.05);
                filter: grayscale(100%);
            }
        }
    }
}